.about-popup {
    background-color: #13405b;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99999;
    padding: 80px 0 105px;
    overflow-y: auto;
    opacity: 0;
    visibility: hidden;
    transition: 0.5s;

    &.open {
        visibility: visible;
        opacity: 1;

        .col-lg-5,
        .col-lg-6 {
            filter: none !important;
            transform: none !important;
            opacity: 1 !important;
            transition: 1s 0.3s;
        }
    }

    .head {
        text-align: center;
        h1 {
            font-size: 40px;
            color: #fff;
            margin-bottom: 13px;
            font-family: bold;
            position: relative;

            @include Sm {
                display: flex;
                flex-direction: column-reverse;
            }

            img {
                position: absolute;
                top: 0;
                right: 0;
                cursor: pointer;

                @include Sm {
                    position: unset;
                    display: block;
                    margin: auto;
                    margin-bottom: 20px;
                }
            }
        }

        p {
            font-size: 16px;
            font-family: med;
            color: #fff;
            margin-bottom: 70px;
        }
    }

    .row {
        display: flex;
        justify-content: space-between;

        .col-lg-5 {
            &:first-child {
                .pic {
                    img {
                        width: 75px;
                        height: 60px;
                    }
                }
            }
            .pic {
                background-color: $color-sec;
                @extend .Flex;
                flex-direction: column;
                height: 259px;
                border-radius: 0 70px 0 70px;
                transform-style: preserve-3d;

                img {
                    transform: translateZ(20px);
                }

                h2 {
                    transform: translateZ(20px);
                    display: block;
                    width: 100%;
                    margin-top: 24px;
                    font-size: 35px;
                    color: #fff;
                    font-family: bold;
                    text-align: center;
                }
            }

            &:nth-of-type(4) {
                .pic {
                    background-color: transparent;
                    border: 6px solid $color-sec;

                    h2 {
                        color: $color-sec;
                    }
                }
            }
        }

        .col-lg-6 {
            h2 {
                font-size: 35px;
                font-family: bold;
                color: $color-sec;
                @extend .after;
            }

            p {
                font-size: 18px;
                font-family: med;
                color: #fff;
                line-height: 35px;
            }
        }

        .col-lg-5,
        .col-lg-6 {
            transform: translateY(100px) scale(1.2);
            opacity: 0;
            margin-bottom: 70px;
        }
    }

    .numbers {
        padding-top: 56px;

        .col-lg-4 {
            text-align: center;

            &:hover {
                span {
                    text-shadow: 3px 5px 1px rgba($color-white, 0.5);
                }
            }

            span {
                font-size: 100px;
                font-family: bold;
                color: #fff;
                display: block;
                line-height: 1;
                transition: 0.5s;
            }

            p {
                font-size: 30px;
                font-family: med;
                color: $color-sec;
            }
        }
    }
}
